html,
body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  display: flex;
  flex-direction: column;
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
  scroll-behavior: smooth;
}

#root {
  flex: 1 0 auto;
}

#page-footer {
  flex-shrink: 0;
}

/* Variables */
:root {
  /* font */
  --main-font: "Indie Flower", cursive;
  --main-bg-color: #fdf6b7;
  --secondary-bg-color: #fbfff8;
  --main-text-color: #000000de;
  --secondary-text-color: #2abcf2;
}

/* CSS reset */
body,
div {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Indie Flower", cursive;
  font-family: var(--main-font);
  background-color: #fdf6b7;
  background-color: var(--main-bg-color);
  overflow-x: hidden;
}

.App {
  text-align: center;
}

.svg-header {
  fill: #fdf6b7;
  fill: var(--main-bg-color);
}

.containerGrid {
  padding-left: 3em;
  padding-right: 3em;
}

footer {
  background-color: #fbfff8;
  background-color: var(--secondary-bg-color);
  border-top: 4px solid #2abcf2;
  border-top: 4px solid var(--secondary-text-color);
  margin-top: 2em;
}

